.Controls {
  display: flex;
  flex-wrap: wrap;
  padding: 5px;
}

.Controls button {
  padding: 10px 25px;
  border-radius: 4px;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  width: calc( 50% - 10px );
  margin: 5px;
  border: 2px solid rgba( 0,0,0,.8 );;
  color: rgba( 0,0,0,.8 );
  background-color: white;
}

.Controls button:active {
  background-color: rgba( 0,0,0,.8 );
  color: white;
}

.Controls button .svg-inline--fa {
  width: 25px;
  height: 25px;
  margin-bottom: 10px;
}
