:root {
  --heading-color: #333;
  --text-color: #333;
  --text-font: "Baloo Chettan";
  --highlight-color: ;
  --app-background: white;
  --background: #5a5a5a;

  /* Pallet */
  --red: #ff3d1e;
  --yellow: #ff911c;
  --green: #1c9600;
}

body {
  margin: 0;
  padding: 0;
  color: #333;
  color: var( --text-color );
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  letter-spacing: 1px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1,h2,h3,h4,h5,h6 { color: #333; color: var( --heading-color ); }

button { color: #333; color: var( --text-color ); outline: none; }

ul { list-style: none; }
a { text-decoration: none; }
a:visited { color: currentColor; }
button { -webkit-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; }

.App {
  text-align: center;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex-flow: column;
          flex-flow: column;
  background: var( --app-background );
}

.Header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  height: 70px;
  border-bottom: 2px solid var( --background );
  background: cornflowerblue;
  max-width: 612px;
  width: 100%;
  margin: 0 auto;
  padding: 0 10px;
}

.Header > * { width: 33.33%; }
.Header * { color: white; }
.Header .logo {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  color: #00631b;
  cursor: pointer;
}

.Main {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  max-width: 1200px;
  margin: auto;
  background: rgba( 255,255,255,0 );
}

.Footer {
  background-color: #333;
  color: white;
  padding: 10px;
  font-size: 14px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-family: "Ubuntu", "Arial", sans-serif;
}

.Footer > p { width: 50%; padding: 10px; display: -webkit-flex; display: flex; -webkit-align-items: center; align-items: center; -webkit-justify-content: flex-start; justify-content: flex-start; }
.Footer > p:first-child { -webkit-justify-content: flex-end; justify-content: flex-end; }
.Footer svg { width: 20px; margin: 0 5px; }
.Footer .react-link { margin-left: 4px; }
.Main {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
}
.Creature {
  position: relative;
  height: 300px;
  width: calc( 100% - 20px );
  border: 5px solid var( --background );
  border-radius: 5px;
  padding: 40px;
  margin: 10px auto;
  box-sizing: border-box;
  background: white;
  overflow: hidden;
}

.Creature img {
  width: 200px;
  height: 200px;
}

.Creature .sun {
  left: 0;
  position: absolute;
  height: 40px;
  width: 40px;
  -webkit-transition: all 1s linear;
  transition: all 1s linear;
}

.Creature .moon {
  position: absolute;
  top: -50px;
  right: 20px;
  height: 40px;
  width: 40px;
  -webkit-transition: all 1s linear;
  transition: all 1s linear;
}
.Indicators { 
  background: white;
  border-top: 2px solid var( --background );
  border-bottom: 2px solid var( --background );
  padding: 10px 0;
}

.Indicators .indicator {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 10px;
  -webkit-justify-content: center;
          justify-content: center;
}

.Indicators .indicator > * { width: 50%; }
.Indicators .indicator h5 { margin-right: 10px; text-align: right; font-size: 16px; }
.Indicators .indicator .bars { display: -webkit-flex; display: flex; }
.Indicators .indicator .bar {
  height: 18px; width: 8px; 
  border-radius: 1px;
  background: #ccc;
  margin: 0 1px;
}
.Indicators .indicator .bar.red { background: var( --red ); }
.Indicators .indicator .bar.yellow { background: var( --yellow ); }
.Indicators .indicator .bar.green { background: var( --green ); }
.Controls {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  padding: 5px;
}

.Controls button {
  padding: 10px 25px;
  border-radius: 4px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: calc( 50% - 10px );
  margin: 5px;
  border: 2px solid rgba( 0,0,0,.8 );;
  color: rgba( 0,0,0,.8 );
  background-color: white;
}

.Controls button:active {
  background-color: rgba( 0,0,0,.8 );
  color: white;
}

.Controls button .svg-inline--fa {
  width: 25px;
  height: 25px;
  margin-bottom: 10px;
}

