.App {
  text-align: center;
  min-height: 100vh;
  display: flex;
  justify-content: space-between;
  flex-flow: column;
  background: var( --app-background );
}

.Header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 70px;
  border-bottom: 2px solid var( --background );
  background: cornflowerblue;
  max-width: 612px;
  width: 100%;
  margin: 0 auto;
  padding: 0 10px;
}

.Header > * { width: 33.33%; }
.Header * { color: white; }
.Header .logo {
  display: flex;
  align-items: center;
  color: #00631b;
  cursor: pointer;
}

.Main {
  flex-grow: 1;
  max-width: 1200px;
  margin: auto;
  background: rgba( 255,255,255,0 );
}

.Footer {
  background-color: #333;
  color: white;
  padding: 10px;
  font-size: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Ubuntu", "Arial", sans-serif;
}

.Footer > p { width: 50%; padding: 10px; display: flex; align-items: center; justify-content: flex-start; }
.Footer > p:first-child { justify-content: flex-end; }
.Footer svg { width: 20px; margin: 0 5px; }
.Footer .react-link { margin-left: 4px; }