.Creature {
  position: relative;
  height: 300px;
  width: calc( 100% - 20px );
  border: 5px solid var( --background );
  border-radius: 5px;
  padding: 40px;
  margin: 10px auto;
  box-sizing: border-box;
  background: white;
  overflow: hidden;
}

.Creature img {
  width: 200px;
  height: 200px;
}

.Creature .sun {
  left: 0;
  position: absolute;
  height: 40px;
  width: 40px;
  transition: all 1s linear;
}

.Creature .moon {
  position: absolute;
  top: -50px;
  right: 20px;
  height: 40px;
  width: 40px;
  transition: all 1s linear;
}